  border-bottom: 1px solid #e1e1e1; }

.search .content-area {
  padding: 40px 40px 40px 0;
  flex: 0 0 70%; }

.search .hentry {
  margin: 40px 0; }

.search .entry-title {
  font-size: 1.7rem; }
  .search .entry-title a {
    color: #248A83;
    text-decoration: none; }
    .search .entry-title a:hover {
      color: #2f1339; }

.search .entry-summary a {
  display: inline-block;
  color: #404040;
  text-decoration: none;
  font-family: "Novecento Sans", serif;
  font-size: 1rem;
  margin-top: 20px;
  padding: 5px 20px;
  font-weight: 400;
  border: 1px solid black; }
  .search .entry-summary a:hover {
    background-color: #248A83;
    color: #fff;
    border: 1px solid #248A83; }

/*--------------------------------------------------------------
# footer
--------------------------------------------------------------*/
.site-footer {
  background-image: url("../../images/dark-wood@2x.png");
  overflow: auto; }
  .site-footer h2 {
    color: #d99054;
    font-size: 21px;
    margin-top: 0px; }
  .site-footer p {
    color: #fff; }
    .site-footer p a {
      color: #fff;
      text-decoration: none; }
    .site-footer p .fa {
      width: 20px; }
    .site-footer p .day-of-week {
      font-weight: 700; }
  .site-footer .footer-social-media .fa {
    font-size: 1.5rem;
    color: #fff;
    margin-right: 10px; }
  .site-footer .site-footer-items {
    padding: 60px 0; }
  .site-footer .footer-items:last-child {
    text-align: right; }
    .site-footer .footer-items:last-child img {
      width: 300px;
      display: inline-block; }
  .site-footer .footer-copyright {
    margin-bottom: 0; }
